<template>
	<div>
		<component
			v-bind:is="columnContent"
			:fieldValue="params.value"
			:fieldSettings="params.settings"
			:mode="mode"
			:view="view"
			@onChange="changeValue"
			@openEdit="openEdit"
		></component>
	</div>
</template>
<script>
	import Vue from 'vue';
	export default
	{
		props: ['params','mode','view','fieldName'],
		computed:
		{
			isCustom()
			{
				return this.params.settings.type == 'custom';
			},
			/**
			 * –î–∏–Ω–∞–º–∏—á–µ—Å–∫–∏ –æ—Ç–æ–±—Ä–∞–∂–∞–µ–º–∞—è —è—á–µ–π–∫–∞ —Ç–∞–±–ª–∏—Ü—ã
			 */
			columnContent()
			{
				if(this.params.settings.type == 'custom')
				{
					// return new Vue({render (h) {return h('div', this.hi) } });
					// return Vue.component('terms-of-service', {template: ` <div v-once> <h1>–£—Å–ª–æ–≤–∏—è –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è</h1> ... –º–Ω–æ–≥–æ-–º–Ω–æ–≥–æ —Å—Ç–∞—Ç–∏—á–µ—Å–∫–æ–≥–æ –∫–æ–Ω—Ç–µ–Ω—Ç–∞ ... </div> ` });
					// return false;
					// debugger;
					return eval(this.params.settings.fieldJs)
				}
				if (typeof this.fieldName == 'undefined' ||  this.fieldName === false )
					return false;

				return () => import(`@/components/fields/${this.fieldName}/Field.vue`);
			}
		},
		methods:
		{
			/**
			 * –û—Ç—Å–ª–µ–∂–∏–≤–∞—Ç—å –∏–∑–º–µ–Ω–µ–Ω–∏—è –≤ —Ñ–∏–ª–¥–µ
			 * @value {
			 *        value
			 *        settings (main format)
			 * }
			 */
			changeValue(value)
			{
				this.$emit('onChange', value);
			},

			/**
			 * Opens edit page
			 */
			openEdit()
			{
				this.$emit('openEdit');
			}
		}
	}
</script>